import React from "react"
import styles from "./styles.module.scss"
import {getNameFromEmail} from "../../../helper/utils"
import http from '../../../services/http'
import {logService} from '../../../services/log'

//resources
import LeaveIcon from '../../../resources/images/leave-ic.svg'
import LockIcon from '../../../resources/images/lock-ic.svg'
import UnlockIcon from '../../../resources/images/unlock-ic.svg'

//components
import UserInRoom from "./UserInRoom"
import {Button} from '../../core/Button'

const CurrentRoom = ({data, agoraClient, showMessage, auth}) => {
    const handleLeaveRoom = async () => {
        const Log = data && logService.createLog("Room", {logToServer: true, roomId: data.id});

        try {
            /**
             * Using Agora
             */
            agoraClient.stop();
            await http.post('api/room/leave');
            // Toast.info('You left the room.');
            showMessage({
                type: "warning",
                value: "You left the room."
            })
            Log.info(`leave_room_success, roomID: ${data.id}`);
        } catch (e) {
            // Toast.error('Couldn\'t leave the room. Please try again.');
            showMessage({
                type: "error",
                value: "Couldn\'t leave the room. Please try again."
            })
            Log.error(`leave_room_failed, room = ${data.id}`);
        }
    }

    const handleLockRoom = async () => {
        const Log = data && logService.createLog("Room", {logToServer: true, roomId: data.id});

        try {
            await http.post('api/room/lock-status');
            // Toast.info('You left the room.');
            showMessage({
                type: "success",
                value: `You ${data.isLocked ? 'locked' : 'unlocked'} the room`
            })
            Log.info(`toggle_room_lock_ok, roomID: ${data.id}`);
        } catch (e) {
            // Toast.error('Couldn\'t leave the room. Please try again.');
            showMessage({
                type: "error",
                value: "Couldn\'t change lock status of the room. Please try again."
            })
            Log.error(`toggle_room_lock_failed, room = ${data.id}`);
        }
    }

    function LockComp(props) {
        const lockState = props.lockState;
        console.log(lockState);
        if (!lockState) {
            return (
                <Button onClickAsync={handleLockRoom} className={styles.buttonLockRoom}>
                    <img src={LockIcon}/>
                    Lock this room
                </Button>
            )
        }
        return (
            <Button onClickAsync={handleLockRoom} className={styles.buttonLockRoom}>
                <img src={UnlockIcon}/>
                Unlock
            </Button>
        );
    }

    return (
        <div className={styles.container}>
            <div className={styles.users}>
                {
                    data.users.map(usr =>
                        <UserInRoom
                            key={usr.id}
                            name={getNameFromEmail(usr.email)}
                            id={usr.id}
                            agoraUserId={usr.agoraUserId}
                            agoraClient={agoraClient}
                            auth={auth}
                        />
                    )
                }
            </div>

            <div className={styles.action}>
                <LockComp lockState={data.isLocked}/>
                <Button onClickAsync={handleLeaveRoom} className={styles.button}>
                    <img src={LeaveIcon}/>
                    Leave
                </Button>
            </div>
        </div>
    )
}

export default CurrentRoom